.job-details-content .section-title .job-metas {
    margin-bottom: 35px;
}

.job-details-metas-wrap{
    border: 1px solid #ECECEC;
    padding: 35px 20px;
    margin-bottom: 45px;
}

.job-details-metas{
    font-size: 16px;
    color: #545454;
    display: flex;
    max-width: 100%;
    flex-wrap: wrap;
    margin-bottom: -50px;
}

.job-details-metas > div{
    flex: 0 0 33.33333%;
    max-width: 33.333333%;
    padding-bottom: 50px;
}

.job-details-metas > div > span{
    display: block;
}
.job-details-metas > div > span.with-icon{
    display: inline-flex;
    line-height: 24px;
    vertical-align: middle;
    color: #B0B0B0;
    margin-bottom: 5px;
}
.job-details-metas > div > span.with-icon img{
    margin-right: 5px;
}

.job-details-metas > div > span.value{
    line-height: 24px;
    vertical-align: middle;
    padding-left: 30px;
}

.job-details-desciption{
    padding-bottom: 40px;
}
.job-details-desciption:last-of-type{
    padding-bottom: 0;
}
.job-details-desciption h4{
    font-size: 22px;
    font-weight: 600;
    color: #000000;
    padding-bottom: 10px;
}
.job-details-desciption p{
    font-size: 16px;
    color: #6E7775;
    padding-bottom: 10px;
}
.job-details-desciption p:last-child{
    padding-bottom: 0;
}


.job-desc-list{
    padding-bottom: 20px;
}
.job-desc-list > li {
    font-size: 16px;
    line-height: 24px;
    color: #6E7775;
    padding-left: 30px;
    padding-bottom: 10px;
    position: relative;
}
.job-desc-list > li:last-child{
    padding-bottom: 0;
}

.job-desc-list > li::before{
    content: '\F285';
    display: inline-block;
    font-family: bootstrap-icons !important;
    font-style: normal;
    font-weight: normal !important;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    vertical-align: -.125em;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 16px;
    position: absolute;
    left: 0;
    top: 4px;
}


.divider{
    border-bottom: 1px solid #ECECEC;
}


.job-details-content .section-title{
    position: relative;
}


@media screen and (max-width: 991px){
    .job-details > .container > .row > [class*="col"]:first-child{
     margin-bottom: 30px;   
    }
}


@media screen and (max-width:767px){
    .job-details-metas{
        margin-bottom: -20px;
    }
    .job-details-metas > div{
        flex: 0 0 50%;
        max-width: 50%;
        padding-bottom: 20px;   
    }
    .job-details-content .section-title .job-metas{
        margin-bottom: 20px;
    }
    .job-details-metas-wrap{
        margin-bottom: 25px;
    }
    .job-details-desciption{
        padding-bottom: 25px;
    }
}